<template>
    <div>
      <header-component title="绑定安全手机"></header-component>
      <router-view></router-view>
      <div class="am-top">
        <div class="am-form-box">
          <label class="zhanghao">手机号</label>
          <div class="am-account">
            <input type="number" class="txtSize" id="phoneInput" v-model="phoneNum" placeholder="请输入您的手机号码" oninput="if(value.length>11)value=value.slice(0,11)">
            <img class="am-account-cancel" @click="clearNum" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <label class="zhanghao">验证码</label>
          <div class="code">
            <input type="text" class="code-input" id="code"  placeholder="输入验证码" maxlength="6">
            <a @click="sendCode" v-show="show" id="sendCode" class="send-code" href="javascript:;">获取验证码</a>
            <span v-show="!show" class="count">({{count}})重新发送</span>
          </div>
          <p class="prompt"><img src="../../assets/images/Login/Login_08.png"/><a>温馨提示：为了您的账户安全，建议您绑定</a></p>
          <p class="am-loginBtn">
            <button @click="bindBtn" type="button" class="am-btn-default">确定绑定</button>
            <button type="button" @click="skip" class="skip">跳过</button>
          </p>
        </div>

      </div>
    </div>
</template>

<script>
  import HeaderComponent from './header.vue'
  var urlhost = process.env.API_AJAX;
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
    export default {
      name:'bind',
        data() {
            return {
              phoneNum:null,
              show: true,
              count: '',
              timer: null,
            }
        },
        components: {
            HeaderComponent,
        },
      mounted:function () {
        var _this = this;
        var user_id = getCookie('userId');
        $.ajax({
          type: 'GET',
          url: urlhost+"usercenter/isbind?user_id="+user_id,
          data$Type: "jsonp",
          data: {
          },
          success: function (response) {
            var mess = JSON.parse(response);
            console.log(mess);
            /*if (mess.status === 0) {
              _this.$alert(mess.message).then(
                function () {
                  _this.$router.push({path:'/usercenter/userInfo'})
                }
              )
            } else {
              return false;
            }*/
          },
        });

      },
      methods:{
        clearNum:function(){
          this.phoneNum = "";
        },
        sendCode:function () {
          var _this = this;
          var phone = _this.phoneNum;
          if (phone === '') {
            _this.$layer.msg("绑定的手机号不能为空!")
          } else {
            $.ajax({
              type: 'POST',
              url: urlhost+"usercenter/bindcode",
              data$Type: "jsonp",
              data: {
                'phone': phone,
              },
              success: function (response) {
                var mess = JSON.parse(response);
                if (mess.status === 0) {
                _this.$layer.msg(mess.message)
              } else {
                _this.$layer.msg(mess.message)
                const TIME_COUNT = 60;
                if (!_this.timer) {
                  _this.count = TIME_COUNT;
                  _this.show = false;
                  _this.timer = setInterval(() => {
                    if (_this.count > 0 && _this.count <= TIME_COUNT) {
                      _this.count--;
                    } else {
                      _this.show = true;
                      clearInterval(_this.timer);
                      _this.timer = null;
                    }
                  }, 1000)
                }
              }
              },
            });

          }
        },
        bindBtn:function () {
          var _this = this;
          var userId = getCookie("userId")
          var phone = $("#phoneInput").val();
          var code = $("#code").val();
          if(phone === '' || code === ''){
            _this.$layer.msg("内容不能为空!")
          }else{
            $.ajax({
              type: 'POST',
              url: urlhost+"usercenter/bindphone?user_id="+userId,
              data$Type: "jsonp",
              data: {
                'phone': phone,
                'code':code
              },
              success: function (response) {
                var mess = JSON.parse(response);
                if (mess.status == 0) {
                  _this.$layer.msg(mess.message)
                }else{
                  _this.$layer.msg(mess.message);
                _this.$router.push({path:'/'})
                }
              },
            });
          }
        },
        skip:function () {
          var _this = this;

          _this.$confirm('为了您的账户安全，您确定跳过此步骤吗!!!').then(
            function () {
              _this.$router.push({path:'/'})
            }
          ).catch(
            function () {
            return false
          })


        },


      }
    }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };



  .am-top{
    width: 90%;
    margin: auto;
    margin-top: n(120);
  }

  .am-form-box{
    width: 100%;
    margin-bottom: 0;
  }

  .am-account{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 90%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    img{
      width: n(50);
      height: n(50);
    }
  }

  .zhanghao{
    margin-top: n(20);
    font-weight: bold;
    display: inline-block;
    font-size: n(24);
    color: #424242;
  }
  .mima{
    @extend .zhanghao
  }

  .code{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 70%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    a{
      width: 25%;
      text-align: right;
      display: block;
      border-left:1px solid #f3f3f3;
      color: green;
    }
  }

  .am-form-box .am-passWord{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 70%;
      height: n(70);
      font-size: n(30);
      color: #b0b0b0;
    }
    img{
      width: n(50);
      height: n(50);
    }
  }


  .am-loginBtn{
    width: 100%;
    margin: n(80) 0 0 0;
  }

  .am-loginBtn button{
    width: 100%;
    height: n(80);
    background: #fee837;
    font-size: n(30);
    border: none;
  }






  .prompt{
    height: n(80);
    line-height: n(80);
    img{
      width: n(50);
      height: n(50);
    }
    a{
      color: #b6b6b6;
    }
  }
  .skip{
    margin-top: n(45);
  }
</style>



